export function re(st, pr, cd) {
    let i = 0;
    const newArr = st.map((item) => ({
        ...item,
        value: item.value.map((serv) => ({
            ...serv,
            price: pr[i],
            code: cd[i],
            _id: cd[i++].split(".").join("")
        }))
    }));
    i = 1800;
    const newArr2 = newArr.map((item) => ({
        ...item,
        value: item.value.map((serv) => {
            if (serv._id === "null") {
                return { ...serv, _id: String(i++) };
            } else return serv;
        })
    }));
    console.log(JSON.stringify(newArr2));
}

